    background: -webkit-linear-gradient(left, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
    background: -o-linear-gradient(left, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
    background: linear-gradient(90deg, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
	width: 100%;
	height: 50px;
	-ms-flex-align: center;
	    align-items: center;
}
.link {
	display: -ms-flexbox;
	display: flex;
	color: white;
}
.github {
	margin-left: 50px;
}


