.navbar {
	background: rgb(34,60,195);
    background: -webkit-gradient(linear, left top, right top, from(rgba(34,60,195,1)), color-stop(30%, rgba(38,176,203,1)), to(rgba(45,142,253,1)));
    background: -webkit-linear-gradient(left, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
    background: -o-linear-gradient(left, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
    background: linear-gradient(90deg, rgba(34,60,195,1) 0%, rgba(38,176,203,1) 30%, rgba(45,142,253,1) 100%);
	width: 100%;
	height: 100px;
	padding: 0px 20px;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}
.scoreboard, .prompt, .appTitle {
	display: -ms-flexbox;
	display: flex;		
}
.prompt {
	font-size: 30px;
}
.scoreboard {
	margin-right: 80px;
	font-size: 30px;
}
.southParkLogo {
	height: 200px;
	margin-left: 30px;
}




